body{
  background-color: #181818;
  overflow-x: hidden;
}
.logoBrand{
  width: 150px;
}
.header{
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding-bottom: 20px;
}
input{
  width: 50%;
  background-color: #121212;
  padding: 15px;
  border: none;
  font-size: 15px;
  color: #fff;
}
.profile{
  margin-left: 20px;
}
.profile > img{
  width: 40px;
  border-radius: 50px;
}
.functions{
  cursor: pointer;
}
.noResult{
  color: #fff;
  font-family: monospace;
  font-size: 20px;
}
.videoMain{
  display: flex;
  justify-content: space-around;
}

iframe{
  width: 850px;
  height: 500px;
}
.videoSec{
  padding: 0 20px;
}
.videoLists > div{
  float: right;
  width: 80%;
}

.videoInfo > .channelTitle, .titleVideo{
  color: #fff;
  font-family: system-ui;
}
.descriptionVideo{
  color: #Fff;
  font-family: system-ui;
  width: 75%;
}
.videoItems{
  display: inline-block;
  cursor: pointer;
  padding: 0 0 15px;
}